:root{
    --black: rgb(0,0,0);
    --red: rgb(255,0,0);
    --white: rgb(255,255,255);
}
.auth{
    background-image: linear-gradient(to top left, var(--black), var(--red));
    height: 100vh;
}

.auth__form{
    display: flex;
    height: 100%;
    align-items: center;
    flex-direction: column;
    justify-content: center;
}

.auth__input{
    padding: 2rem 4rem;
    border: none;
    font-size: 1.6rem;
    text-transform: uppercase;
    border-bottom: 5px solid var(--black);
    margin-bottom: 3rem;
    width: 50vw;
    border-radius: 3px;

}

.auth__button,
.auth__button:link,
.auth__button:visited{
    padding: 2rem 4rem;
    border: none;
    font-size: 1.6rem;
    text-transform: uppercase;
    width: 50vw;
    cursor: pointer;
    border-radius: 3px;
    background-image: linear-gradient(105deg, var(--white) 50%, var(--red)50%);
    background-size: 220%;
    transition: all 1s;
}

.auth__button:hover{
    background-position: 100%;
    color:var(--white);
}

@media only screen and (max-width: 500px) {
    .auth__button, .auth__input{
        width: 100%;
    }
}