.chat{
    height:85vh;
}
.chat__heading{
    font-size: 3rem;
    text-transform: uppercase;
    margin: 3rem;
    font-weight: 900;
    text-align: center;
}

.chat__secondary{
    font-size: 1.6rem;
    text-transform: uppercase;
    margin-bottom: 1rem;
    font-weight: 700;
    text-align: center;
}

.chat__pane{
    height: 68vh;
    overflow-y: scroll;
    width: 50%;
    margin: auto;
    display: block;
}

.chat__description{
    font-size: 2rem;
    line-height: 3rem;
    list-style: none;
}
.chat__form{
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: flex-end;
}

.chat__input{
    padding: 2rem 4rem;
    border: none;
    font-size: 1.6rem;
    border-bottom: 5px solid var(--black);
    margin-bottom: 3rem;
    width: 50vw;
    border-radius: 3px;
}

.chat__input:focus{
    outline: none;
}

.chat__button,
.chat__button:link,
.chat__button:visited{
    padding: 2rem 4rem;
    border: none;
    font-size: 1.6rem;
    text-transform: uppercase;
    width: 50vw;
    cursor: pointer;
    border-radius: 3px;
    background-image: linear-gradient(105deg, var(--red) 50%, var(--black)50%);
    background-size: 220%;
    transition: all 1s;
    color:var(--white);
}

.chat__button:hover{
    background-position: 100%;
    color:var(--white);
}

.chat__border{
    width: 50%;
    margin:auto;
}

.usernameBlue{
    color:blue;
    text-transform: uppercase;
}

.usernameRed{
    color:var(--red);
    text-transform: uppercase;
}

.spinner{
    height:5rem;
    width: 5rem;
    border-radius: 50%;
    border: 3px solid var(--black);
    border-bottom: 3px solid transparent;
    animation: spin 1s ease-out infinite;
    animation-timing-function: linear;
    margin:50% auto;

}

@keyframes spin {
    0%{
        transform: rotate(0deg);
    }

    100%{
        transform: rotate(360deg);
    }
}

@media only screen and (max-width: 500px) {
    .chat__button, .chat__input,.chat__border{
        width: 95%;
    }

    .chat__pane{
        height: 55vh;
        width: 100vw;
        padding: 1rem;
    }
}